import React,{Fragment} from 'react'
import { Form, Input, Button, Checkbox,Row, Col,Upload } from 'antd';
import Axios from 'axios';
const createForm = Form.create;
const FormItem = Form.Item;
function getCookie(name) 
{ 
    var arr,reg=new RegExp("(^| )"+name+"=([^;]*)(;|$)");
 
    if(arr=document.cookie.match(reg))
 
        return unescape(arr[2]); 
    else 
        return null; 
} 
class Login extends  React.Component {
  constructor(props){
    super(props)
    this.handleSubmit=this.handleSubmit.bind(this)
    this.toindex=this.toindex.bind(this)
    
  }
  
  handleSubmit(e) {
    e.preventDefault();
    
    var username=this.props.form.getFieldValue("username");
    var password=this.props.form.getFieldValue("password");
    var url="/admin/login"
    var data={
      "username":username,
      "paswd":password
    }
    var history=this.props.history
    Axios.post(url,data).then(
      function(response){
        if(response.data.code==0){
         
          history.push("/index")
        }
      })
   

  }
  toindex(){
    console.log(this.props)
  }

      render(){
        const { getFieldProps } = this.props.form;
        return (
          
          <div style={{paddingTop:100,"padding-left":"30%"}}>
          
          <div style={{height:300,width:300,}}>

          <Form inline onSubmit={this.handleSubmit.bind(this)}>
            <FormItem
              label="">
              <Input placeholder=""
                {...getFieldProps('username')} />
            </FormItem>
            <FormItem
              label="">
              <Input type="password" placeholder=""
                {...getFieldProps('password')} />
            </FormItem>
            <Button type="primary" htmlType="submit"></Button>
            
          </Form>
          </div>
          </div>
        );

      }

        
    
}

Login = createForm()(Login);

export default Login;