import React,{Fragment} from 'react'
import { Row,Breadcrumb,Table, Icon,Input,Col, Card, Form,message, InputNumber ,Button, Modal} from 'antd';
import Axios from 'axios';


class Addauto extends React.Component{
  
  
    handleSubmit = e => {
        var data={}
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
         var objid=parseInt(values["objid"])
         var interval=values["interval"]
         var click=values["click"]
      
          data={"objid":objid,"interval":interval,"click":click}
        
        });
        var url="/auto/add"
        Axios.post(url,data).then((response)=>{
             
            if(response.data.code==0){
                message.success("")
            }
          })
      };
    render(){

        const formItemLayout = {
            labelCol: { span: 6 },
            wrapperCol: { span: 14 },
          };
        const { getFieldDecorator } = this.props.form;
        return(
               
                        <Form {...formItemLayout} onSubmit={this.handleSubmit}>
                        <Form.Item  label="id">
                            {getFieldDecorator('objid', {
                            rules: [
                            {
                             
                                message: 'The input is not valid E-mail!',
                            },
                            {
                                required: true,
                                message: 'ID',
                            },
                            ],
                        })(<Input />)}

                        </Form.Item>
                        <Form.Item label="">
                        {getFieldDecorator('interval', {
                            rules: [
                            {
                                required: true,
                                message: '',
                              
                            },
                            
                            ],
                        })(<InputNumber />)}
                        </Form.Item>
                        <Form.Item label="">
                        {getFieldDecorator('click', {
                            rules: [
                            {
                                
                                required: true,
                                message: '',
                            },
                           
                            ],
                        })(<InputNumber />)}
                        </Form.Item>
                    
                      
                        <Form.Item wrapperCol={{ span: 12, offset: 6 }}>
                        <Button type="primary" htmlType="submit"></Button>
                        </Form.Item>
                       
                        </Form>
           


           
        )
    }
}
const createForm = Form.create;

Addauto = createForm()(Addauto);
export default Addauto