import React,{Fragment} from 'react'
import { Row,Breadcrumb,Table, Icon,Input,Col, Button,InputNumber, message,Popconfirm,Tag, Modal,Form} from 'antd';
import Axios from 'axios'
import {Link} from 'react-router-dom'
import Temp from './temp'
import moment from 'moment';
class CustomerList extends React.Component{

  constructor(props){
    super(props)
    var id=0
    
    this.getlist=this.getlist.bind(this)
    this.plustitck=this.plustitck.bind(this)
    this.handleChange=this.handleChange.bind(this)
    this.stopvote=this.stopvote.bind(this)
    this.setVotestaus=this.setVotestaus.bind(this)
    var page=parseInt(this.props.match.params.page)
    var data=this.props.location.query
    console.log(data)
    var mao
    if(data){

      mao=data.mao
    }else{
      mao=""
    }
    this.state={
      id:parseInt(this.props.match.params.id),
      data:[],
      pagination: {"current":page},
      loading: false,
      Titckinput:1,
      objtitle:"",
      value:"",
      search:"",
      star:0,
      mao:mao,
      staus:"",
      visible:false,
      objid:parseInt(this.props.match.params.id),

    }
    this.getobject(parseInt(this.props.match.params.id))
    
    this.getlist(page,"",this.state.id)
  }
  componentDidUpdate(){
    console.log(this.state.mao)
    if(this.state.mao){
      this.scrollToAnchor(this.state.mao)
    }
  }
  randotitck(objid,max,min){

    
    Axios.post("/gift/rando",{"objid":this.state.id,"max":max,"min":min}).then((res)=>{
      if(res.data.code==0){
        const pager = { ...this.state.pagination };
        pager.current=1
        this.setState({
          pagination:pager
        })
        this.getlist(1,"",this.state.id)
      }
    })
  }
  plustitck(id,num){

    var url="/gift/plustitck"
    this.setState({ loading: true });
    Axios.post(url,{"id":id,"objid":this.state.id,"num":num}).then(
      (res)=>{
        if(res.data.code==0){
          message.success("")
          var data_=this.state.data
          this.setState({
            data:data_.filter((item) => {
              if (item.ID == id)
              {
                item.Titcks+=num
                
              }
            
              return item
           })
           ,loading:false})
        }else{
          message.error("")
        }
      }
    )
  }
  plusgist(id,num){
    var url="/gift/plusgift"
    this.setState({ loading: true });
    Axios.post(url,{"id":id,"objid":this.state.id,"num":num}).then(
      (res)=>{
        if(res.data.code==0){
          message.success("")
          var data_=this.state.data
          this.setState({
            data:data_.filter((item) => {
              if (item.ID == id)
              {
                item.Moneys+=num
                
              }
            
              return item
           })
           ,loading:false})
        }else{
          message.error("")
        }
      }
    )
  }
  deleteuser= id =>{
    var url="/user/delete"
    var self=this
    Axios.post(url,{"id":id}).then((res)=>{
  
      if(res.data.code==0){
        const data = [...self.state.data];
        this.setState({ data: data.filter(item => item.ID !== id) });
        message.success('');

      }else{
        message.error('');

      }
  
    }
    )
  
  };
  getobject(id){
    var url="/object/get"

     Axios.post(url,{"id":id}).then((res)=>{
      if(res.data.code==0){
        this.setState({
          objtitle:res.data.data.Title,
          star:res.data.data.Star,
          objid:res.data.data.ID,
          
        })
      }
     })
  }
  scrollToAnchor = (anchorName) => {
    if (anchorName) {
      let anchorElement = document.getElementById(anchorName);
      if(anchorElement) { anchorElement.scrollIntoView();
      this.setState({
        mao:""
      }) }
  }
  }
  getlist(page,search,objid) {
    var url="/user/list"
    var self=this
    this.setState({ loading: true });
    Axios.post(url,{"page":page,"search":search,"objid":objid,"sort":"titcks","staus":this.state.staus}).then(
      function(response){


        if(response.data.code==0){
         
          var pagination = { ...self.state.pagination };
          pagination.total = response.data.data.Count;
          var newdatalist=[]
          for(var index in response.data.data.List){
            var newdata=response.data.data.List[index]
            newdata["titckinput"]=0
            newdata["giftinput"]=0
            newdatalist.push(newdata)
          }
          self.setState({
            loading: false,
            data:newdatalist,
            pagination:pagination
          })
          
        }
      })

    }
    handleChange(event) {
      this.setState({value: event.target.value});
    }
    
    handleTableChange = (pagination, filters, sorter) => {
      const pager = { ...this.state.pagination };
      pager.current = pagination.current;
      this.setState({
        pagination: pager,
      });
      this.getlist( pager.current,this.state.search,this.state.id)
    }
    stopvote(uid){
      
      Axios.post("/user/stopvote",{"userid":uid}).then((res)=>{

        if (res.data.code==0){
          message.success("")
          this.setVotestaus(uid,"no")
        }
      })
    }
    keepvote(uid){
      Axios.post("/user/keepvote",{"userid":uid}).then((res)=>{

        if (res.data.code==0){
          message.success("")
          this.setVotestaus(uid,"yes")
        }
      })
    }
    setstar(objid,userid){
      Axios.post("/object/setstar",{"userid":userid,"objid":objid}).then((res)=>{

        if (res.data.code==0){
          message.success("")
          this.setState({
            star:userid
          })
        }
      })
    }
    
    
    setVotestaus(userid,staus){
      var data=this.state.data
      this.setState({
        data:data.filter((item) => {
          if (item.ID == userid)
          {
            item.Votestaus=staus
          }
        
          return item
       })
       })
    }
    render(){
      const columns = [
        {title:"",
        dataIndex:"Mindex",
        key:"Mindex"  ,
      },
        {
        title: '',
        dataIndex: 'Name',
        key: 'Name',
        render(text) {
          return <a href="">{text}</a>;
        }
      },
      {
        title: '',
        dataIndex: 'Image1',
        key: 'Image1',
        render(text) {
          return <img style={{width:100,height:100}} src={text}></img>;
        }
      }, {
        title: '',
        dataIndex: 'Phone',
        key: 'Phone',
      }, {
        title: '',
        dataIndex: 'Titcks',
        key: 'Titcks',
        render:(text, record)=>{
          var id=record.ID
          
            return(
                <span>
                <label>{text}</label>
                <InputNumber style={{width:100}} min={-100000} max={100000} onChange={
                  (value)=>{
                    
                    var data_=this.state.data
                   
                  this.setState({
                   data:data_.filter((item) => {
                     if (item.ID == id)
                     {
                       item.titckinput=value
                     }
                   
                     return item
                  })
                  })
                
                  }
                }  value={record.titckinput} />
                <Button onClick={()=>this.plustitck(record.ID,record.titckinput)} type="primary">+</Button>
                </span>
            )
        }
      }, 
      {
          title: '',
          dataIndex: 'Moneys',
          key: 'Moneys',
          render:(text,record)=>{
            var id=record.ID
            return(
              <span>
              <label>{text}</label>
              <InputNumber style={{width:100}} min={0} max={100000} onChange={
                (value)=>{
                  
                  var data_=this.state.data
                 
                this.setState({
                 data:data_.filter((item) => {
                   if (item.ID == id)
                   {
                     item.giftinput=value
                   }
                 
                   return item
                })
                })
              
                }
              }  value={record.giftinput} />
              <Button onClick={()=>this.plusgist(record.ID,record.giftinput)} type="primary">+</Button>
              </span>
            )
        }
        }, 
       
        {
          title: '',
          dataIndex: 'CreatedAt',
          key: 'CreatedAt',
          render(text){
            var newtext=  moment(text).format('YYYY-MM-DD HH:mm:ss')
            return(newtext)
            
          }
        }, 
        {
            title: '',
            dataIndex: 'staus',
            key: 'staus',
            render:(text, record)=> {
              return(
                <span>
              { record.Staus!="yes"?  <Tag color="#f50"></Tag>:<Tag color="#2db7f5"></Tag> }
              <br/>
               { record.Votestaus!="yes"? <Tag color="#f50"></Tag>:<Tag color="#2db7f5"></Tag> }
               <br/>
               {record.ID==this.state.star?<Tag color="red"></Tag>:null}
               </span>
               
              );
            }
          }, 
      {
        title: '',
        key: 'operation',
        render:(text, record)=> {
          return (
            <span>
              <Link id={record.ID} to={"/addcustomer/"+this.state.id+"/"+record.ID+"/"+this.state.pagination.current}><a href="#"></a></Link>
              <br/>
              <Popconfirm title="?" onConfirm={() =>this.deleteuser(record.ID)}>
            <a href="javascript:;"></a>
            </Popconfirm>
              <br/>
              <span className="ant-divider"></span>
           {record.Votestaus=="yes"?<a onClick={()=>this.stopvote(record.ID)}></a>:<a onClick={()=>this.keepvote(record.ID)}></a>  }
             <br/>
             <Link to={"/usergifts/"+record.ID+"/"+record.Name}><a ></a></Link> 
            </span>
          );
        }
      }];
      
      const formItemLayout = {
        labelCol: { span: 6 },
        wrapperCol: { span: 14 },
      };
    const { getFieldDecorator } = this.props.form;
      
        return(
          <Row>
            <Temp/>
            <Col style={{backgroundColor:"#ffffff"}} span={20}>
            <div style={{width:"100%"}}>
                  <Row>  
                <Breadcrumb>
                <Breadcrumb.Item></Breadcrumb.Item>
                <Breadcrumb.Item></Breadcrumb.Item>
                <Breadcrumb.Item></Breadcrumb.Item>
                <Breadcrumb.Item></Breadcrumb.Item>
                 </Breadcrumb>
             </Row>
             <Row style={{paddingTop:10}} >
                 <Col span="10">
                     <h1>
                         
                     </h1>
                     </Col>
                 <Col span="10"></Col>
             </Row>
             <Row>
             <Col span="10"><h3>{""+this.state.objtitle+""}</h3></Col>
             <Col span="13" style={{textAlign:"right"}}>
               
             <Button type="primary"><a href={"/user/csv/"+this.state.id}></a></Button>
             <Button type="primary" onClick={()=>this.showModal()}>+</Button>
             <Link to={"/addcustomer/"+this.state.id+"/0/"+this.state.page}>  <Button type="primary">+</Button></Link>
           
             <Link to={"/addcustomerlist/"+this.state.id}><Button type="primary">+</Button> </Link>
             </Col>
             </Row>
             <Row>
            <div style={{paddingTop:20,paddingLeft:0}}>
                <Row>
                <Col span="10" > <Input value={this.state.value} onChange={this.handleChange} placeholder="" /></Col>
                <Col span="4" style={{paddingLeft:15}}> <Button onClick={
                   ()=>{
                    const pager = { ...this.state.pagination };
                  
                    const value=this.state.value
                    
                    this.setState(
                      {
                        search:value,
                        pagination:{},
                        data:[],
                      }
                    )
                    
                    this.getlist(1,value,this.state.id)
                  }
                }
                ></Button></Col>
                <Col span="4" style={{paddingLeft:50}}>
                <Button type="primary"  onClick={()=>{
                  if (this.state.staus==""){
                    this.setState({
                    
                        pagination:{},
                        data:[],
                      staus:"no"
                    },()=>{
                      this.getlist(1,"",this.state.id)
                    })
                  }else{
                    this.setState({
                      
                        pagination:{},
                        data:[],
                      staus:""
                    },()=>{
                      this.getlist(1,"",this.state.id)
                    })
                  }
                  
                 
                }}>{this.state.staus==""?"":""}</Button>
                </Col>
          
                </Row>
          
            </div>
          
             </Row>
             <Row>
             <Table columns={columns} 
             dataSource={this.state.data}
             loading={this.state.loading}
             pagination={this.state.pagination}
             onChange={this.handleTableChange}
             />
             </Row>
            </div>
            </Col>
            <Modal  visible={this.state.visible}
                onOk={this.handleOk}
                onCancel={this.handleCancel}>
            <Form {...formItemLayout} onSubmit={this.handleSubmit}>
                        
                        <Form.Item label="">
                        {getFieldDecorator('min', {
                            rules: [
                            {
                                required: true,
                                message: '',
                              
                            },
                            
                            ],
                        })(<InputNumber />)}
                        </Form.Item>
                        <Form.Item label="">
                        {getFieldDecorator('max', {
                            rules: [
                            {
                                
                                required: true,
                                message: '',
                            },
                           
                            ],
                        })(<InputNumber />)}
                        </Form.Item>
                    
                      
                        <Form.Item wrapperCol={{ span: 12, offset: 6 }}>
                        <Button type="primary" htmlType="submit"></Button>
                        </Form.Item>
                       
                        </Form>
           
            </Modal>
            </Row>
        )
    }

    handleSubmit = e => {
      var data={}
      e.preventDefault();
      this.props.form.validateFields((err, values) => {
      
       var max=values["max"]
       var min=values["min"]
       this.randotitck(this.state.id,max,min)
      
      })
   
    };
    showModal = () => {
      this.setState({
        visible: true,
      });
    };
  
    handleOk = e => {
      console.log(e);
      this.setState({
        visible: false,
      });
    };
  
    handleCancel = e => {
      console.log(e);
      this.setState({
        visible: false,
      });
    };
    
}
const createForm = Form.create;

CustomerList = createForm()(CustomerList);

export default CustomerList