import React,{Fragment} from 'react'
import { Row,Breadcrumb,Table, Icon,Input,Col,Button,Popconfirm ,message } from 'antd';
import {Link} from 'react-router-dom'
import Axios from 'axios';
import Temp from './temp'
import copy from 'copy-to-clipboard';
import moment from 'moment';

function fun_date(AddDayCount){
  var dd = new Date();
  dd.setDate(dd.getDate()+AddDayCount);//AddDayCount
  var y = dd.getFullYear();
  var m = (dd.getMonth()+1)<10?"0"+(dd.getMonth()+1):(dd.getMonth()+1);//100
  var d = dd.getDate()<10?"0"+dd.getDate():dd.getDate();//100
  return y+"-"+m+"-"+d;
}
  
class ObjiectList extends React.Component{
  deleteObj= id =>{
    var url="/object/delete"
    var self=this
    Axios.post(url,{"id":id}).then((res)=>{
  
      if(res.data.code==0){
        const data = [...self.state.data];
        this.setState({ data: data.filter(item => item.Id !== id)});
        message.success('');

      }else{
        message.error('');

      }
  
    }
    )
  
  };
   rest(){
    const pager = { ...this.state.pagination };
    pager.current = 1;
    this.setState({
      pagination: pager,
    });
    this.getlist(1,this.state.search)
   }
   copyobj =id=> {
    var url="/object/copy"
    Axios.post(url,{"id":id}).then((res)=>{
      if(res.data.code==0){
       this.rest()

      }else{
        message.error('');

      }
  
    }
    )
  };
  
 
  constructor(props){
    super(props)
    this.getlist=this.getlist.bind(this)
    this.handleChange=this.handleChange.bind(this)

    this.rest=this.rest.bind(this)
   
    this.columns = [{
      title: 'ID',
      dataIndex: 'Id',
      key: 'Id',
    },{
      title: '',
      dataIndex: 'Title',
      key: 'Title',
      render(text,record) {
        return <a onClick={
          ()=>{
            var domain = window.location.host+"/wx/index/"+record.Id;
            copy(domain)
            message.success("")

          }
        }>{text}</a>;
      }
    }, {
      title: '',
      dataIndex: 'Avstart',
      key: 'Avstart',
      render(text){
        var newtext=  moment(text).format('YYYY-MM-DD HH:mm:ss')
        return(newtext)
        
      }
    }, {
      title: '',
      dataIndex: 'Avend',
      key: 'Avend',
      render(text){
        var newtext=  moment(text).format('YYYY-MM-DD HH:mm:ss')
        return(newtext)
        
      }
    }, 
    {
        title: '',
        dataIndex: 'Titcksum',
        key: 'Titcksum',
      }, 
      {
        title: '',
        dataIndex: 'Click',
        key: 'Click',
      }, 
      {
        title: '',
        dataIndex: 'Moneysum',
        key: 'Moneysum',
      }, 
      {
        title: '',
        dataIndex: 'Usernum',
        key: 'Usernum',
      },
    {
      title: '',
      key: 'operation',
      render:(text, record) => {
       console.log(this)
        return (
          <span>
            <Popconfirm title="?" onConfirm={() =>this.deleteObj(record.Id)}>
            <a href="javascript:;"></a>
            </Popconfirm>
            <br/>
            <span className="ant-divider"></span>
            <a onClick={
              ()=>this.copyobj(record.Id)
            }></a>
            <br/>
            <span className="ant-divider"></span>
            <Link to={"/addobject/"+record.Id}></Link>
            <br/>
            <span className="ant-divider"></span>
            <Link to={"/customerlist/"+record.Id+"/1"}></Link>
          </span>
        );
      }
    }];
   this.buildtimebutton=this.buildtimebutton.bind(this)
    
    this.state = {
      value:"",
      search:"",
      data: [],
      rangetime:"",
      pagination: {},
      loading: false,
    };

  }
 
  componentDidMount(){
  
    
      this.getlist(1,this.state.search)
  }
  getlist(page,search) {
    var url="/object/list"
    var self=this
    this.setState({ loading: true });
    Axios.post(url,{"page":page,"search":search,"rangetime":this.state.rangetime}).then(
      function(response){

        if(response.data.code==0){
          var pagination = { ...self.state.pagination };
          pagination.total = response.data.data.Count;
          self.setState({
            loading: false,
            data:response.data.data.List,
            pagination:pagination
          })
        }
      })

    }
    handleTableChange = (pagination, filters, sorter) => {
      const pager = { ...this.state.pagination };
      pager.current = pagination.current;
      this.setState({
        pagination: pager,
      });
      this.getlist( pager.current,this.state.search)
    }
    handleChange(event) {
      this.setState({value: event.target.value});
    }
    buildtimebutton(i){

      var newtimes=fun_date(i)
    return(<Button onClick={()=>{
        this.setState({
          search:"",
          data: [],
          rangetime:fun_date(i),
          pagination: {},
          loading: false,
        }
        ,()=>{
          this.getlist(1,"")
        })
      }
        
      } >{newtimes}</Button>
    )
    
    }
    render(){
      const columns = this.columns.map(col => {
        if (!col.editable) {
          return col;
        }
        return {
          ...col,
          onCell: record => ({
            record,
            editable: col.editable,
            dataIndex: col.dataIndex,
            title: col.title,
            handleSave: this.handleSave,
          }),
        };
      });
      var timedatas=[
        this.buildtimebutton(0),
        this.buildtimebutton(1),
        this.buildtimebutton(2),
        this.buildtimebutton(3),
        this.buildtimebutton(4),
        this.buildtimebutton(5),
        this.buildtimebutton(6)
      ]
     
        return(
          <Row>

            <Temp />
            <Col style={{backgroundColor:"#ffffff"}} span={20}>
            <div style={{width:"100%"}}>
            <Row>  
                <Breadcrumb>
                <Breadcrumb.Item></Breadcrumb.Item>
                <Breadcrumb.Item></Breadcrumb.Item>
                <Breadcrumb.Item></Breadcrumb.Item>
                 </Breadcrumb>
             </Row>
             <Row>
            <div style={{paddingTop:20,paddingLeft:0}}>
                <Row>
                <Col span="10" > <Input value={this.state.value} onChange={this.handleChange} placeholder="" /></Col>
                <Col span="4" style={{paddingLeft:15}}> <Button onClick={
                   ()=>{
                    const pager = { ...this.state.pagination };
                  
                    const value=this.state.value
                    console.log(value)
                    this.setState(
                      {
                        search:value,
                        pagination:{},
                        data:[],
                      }
                    )
                    
                    this.getlist(1,value)
                  }
                }
                ></Button></Col>
                <Col span="8" style={{textAlign:"right"}}><Link to={"/addobject/0"}><Button ></Button></Link></Col>
                </Row>
          
            </div>
          
             </Row>
             <Row>
               {timedatas}
             </Row>
            <Row>
                <Table 
                columns={columns} 
                // rowKey={record => record.login.uuid}
                dataSource={this.state.data}
                loading={this.state.loading}
                pagination={this.state.pagination}
                onChange={this.handleTableChange}
                />
            </Row>
            </div>
            </Col>
            </Row>
        )
    }
}
export default ObjiectList