import React,{Fragment} from 'react'
import ObjiectList from './objectlist'
import CustomerList from './Customerlist'
import Addcustomer from './addcustomer'
import Addobject from './bojectadd'
import Avatar from './addboject'
import GiftList from './giftlist'
import AddAdmin from './addAdmin'
import Admin from './admin'
import { Menu, Icon, Switch,Row, Col  } from 'antd';
const SubMenu = Menu.SubMenu;

var pagemap={
    "objlist":ObjiectList,
    "giftlist":GiftList,
    'customerList':CustomerList,
}
class Index extends React.Component{


    constructor(props){
        super(props)
        this.state={
            index:ObjiectList
        }
    this.menuclick=this.menuclick.bind(this)
    }
    
    menuclick(key){

        console.log(key)
        this.setState({
            index:pagemap[key]
        })
    }
    render(){

        return(
            <Row >
        <Col span={4}>
                        <div>
                      
                        <Menu theme="dark"
                        onClick={this.handleClick}
                        style={{ width: "100%" ,height:"100%"}}
                        defaultOpenKeys={['sub1']}
                      
                        mode="inline">
                       
                        <SubMenu key="sub2" title={<span><Icon type="appstore" /><span></span></span>}>
                        <Menu.Item key="6" onClick={()=>this.menuclick("customerList") }></Menu.Item>

                       
                            </SubMenu>
                      
                        <SubMenu key="sub4" title={<span><Icon type="setting" /><span></span></span>}>
                        <Menu.Item key="8" onClick={()=>this.menuclick("objlist")} >
                        </Menu.Item>
                        <Menu.Item key="7"  onClick={()=>this.menuclick("giftlist") }></Menu.Item>
                        </SubMenu>
                        </Menu>
                    </div>
                </Col>
                <Col style={{backgroundColor:"#ffffff"}} span={20}>
                    {<this.state.index />}
                   
                    </Col>
            </Row>
        )

    }
}

export default Index