import React,{Fragment} from 'react'
import { Menu, Icon, Switch,Row, Col  } from 'antd';
import {Link} from 'react-router-dom'
const SubMenu = Menu.SubMenu;

class Temp extends React.Component{
    
    constructor(props){
        super(props)
    }
    render(){

        return(
            
        <Col span={4}>
                        <div>
                      
                        <Menu theme="light"
                        
                        onClick={this.handleClick}
                        style={{ width: "100%" ,height:"100%"}}
                        defaultOpenKeys={['sub2',"sub4"]}
                      
                        mode="inline">
                       
                        <SubMenu key="sub2" title={<span><Icon type="appstore" /><span></span></span>}>
                        <Menu.Item key="6"><Link to="/admin"></Link></Menu.Item>
                        <Menu.Item key="7"><Link to="/myadmin"></Link></Menu.Item>

                       
                            </SubMenu>
                      
                        <SubMenu key="sub4" title={<span><Icon type="setting" /><span></span></span>}>
                        <Menu.Item key="8" ><Link to="/index"></Link></Menu.Item>
                       <Menu.Item key="9" > <Link to="/gifts">  </Link></Menu.Item>
                       <Menu.Item key="10" > <Link to="/blacklist">  </Link></Menu.Item>
                       <Menu.Item key="11" > <Link to="/auto">  </Link></Menu.Item>
                        </SubMenu>
                        </Menu>
                    </div>
                </Col>
               
         
        )

    }
}

export default Temp