import React,{Fragment} from 'react'
import Temp from './temp'
import { Row,Breadcrumb,Table, Icon,Input,Col, Button,InputNumber,Tag} from 'antd';
import Axios from 'axios'
import moment from 'moment';
class GiftList extends React.Component{

  constructor(props){
    super(props)
    this.state = {
      data: [],
    
      pagination: {},
      loading: false,
    };

  }
  componentDidMount(){
  
    
    this.getlist(1)
}
getlist(page){
  var url="/gift/list"
  var self=this
  this.setState({ loading: true });
  Axios.post(url,{"page":page}).then(
    function(response){
      if(response.data.code==0){
        var pagination = { ...self.state.pagination };
        pagination.total = response.data.data.Count;
        self.setState({
          loading: false,
          data:response.data.data.List,
          pagination:pagination
        })
      }
}
  )
}
handleTableChange = (pagination, filters, sorter) => {
  const pager = { ...this.state.pagination };
  pager.current = pagination.current;
  this.setState({
    pagination: pager,
  });
  this.getlist( pager.current)
}

    render(){

        return(
          <Row>
            <Temp/>
            <Col style={{backgroundColor:"#ffffff"}} span={20}>
                      
                <div style={{width:"100%"}}>
                <Row>  
              <Breadcrumb>
              <Breadcrumb.Item></Breadcrumb.Item>
              <Breadcrumb.Item></Breadcrumb.Item>
              <Breadcrumb.Item></Breadcrumb.Item>
              <Breadcrumb.Item></Breadcrumb.Item>
              </Breadcrumb>
          </Row>
          <Row style={{paddingTop:10}} >
              <Col span="10">
                  <h1>
                      
                  </h1>
                  </Col>
              <Col span="10"></Col>
          </Row>
        

          <Row>
          <Table columns={columns} 
            dataSource={this.state.data}
            loading={this.state.loading}
            pagination={this.state.pagination}
            onChange={this.handleTableChange}
          />
          </Row>
          </div>

      </Col>
      </Row>
        )
    }
}

const columns = [{
    title: '',
    dataIndex: 'Title',
    key: 'Title',
    render(text) {
      return <a href="#">{text}</a>;
    }
  },
  {
    title: '',
    dataIndex: 'Name',
    key: 'Name',
    render(text) {
      return <a href="#">{text}</a>;
    }
  },
  {
    title: '',
    dataIndex: 'Titcknum',
    key: 'Titcknum',
    render(text) {
        return(
        <div>
       <Tag color="#f50">{text}</Tag>
        </div>
        )
   
    }
  }, {
    title: '',
    dataIndex: 'Giftnum',
    key: 'Giftnum',
    render(text) {
        return(
        <div>
        <Tag color="#2db7f5">{text}</Tag>
        </div>
        )
   
    }
  }, {
    title: 'ip',
    dataIndex: 'Ip',
    key: 'Ip',
    render(text){
        return(
            <span>
            {text}
            </span>
        )
    }
  }, 
  {
      title: 'ip',
      dataIndex: 'Ipcity',
      key: 'Ipcity',
    }, 
    {
      title: '',
      dataIndex: 'Staus',
      key: 'Staus',
      render(text){
       
        if(text=="yes"){
           return (<Tag color="#87d068"></Tag>)
        }else{
            return (<Tag ></Tag>)
        }
        
      }
    }, 
    {
      title: '',
      dataIndex: 'UpdatedAt',
      key: 'UpdatedAt',
      render(text){
        var newtext=  moment(text).format('YYYY-MM-DD HH:mm:ss')
        return(newtext)
        
      }
    }, 
    
    {
        title:"",
        dataIndex:"Gifttype",
        key:"Gifttype",
        render(text){
          return(text=="vote"?<Tag color="#87d068"></Tag>:<Tag color="red"></Tag>)
        }
    }
   
  
  ];
 
   
export default GiftList