import React,{Fragment} from 'react'
import { Row,Breadcrumb,Table, Icon,List,Col, Button,InputNumber, message,Popconfirm,Tag} from 'antd';
import Axios from 'axios'
import {Link} from 'react-router-dom'
import Temp from './temp'

class BlaskList extends React.Component{



    constructor(props){
        super(props)
        this.getiplist=this.getiplist.bind(this)
        this.deleteip=this.deleteip.bind(this)
        this.state={
            data:[]
        }
        this.getiplist()
    }

    getiplist(){

        
        Axios.post("/black/list").then((res)=>{
            if(res.data.code==0){
                this.setState({
                    data:res.data.data
                })
            }
        })
    }
    deleteip(ip){
    Axios.post("/black/delete",{"ip":ip}).then((res)=>{

        if(res.data.code==0){
            this.getiplist()
        }
    })
    }
    render(){
      
        var data=this.state.data
        if(!data){
            data=[]
        }
        return(
            <Row>
            <Temp/>
            <Col style={{backgroundColor:"#ffffff"}} span={20}>
            <h3 style={{ margin: '16px 0' }}></h3>
            <List
              size="small"
            
           
              bordered
              dataSource={data}
              renderItem={item => <List.Item><div>{item}</div><Icon onClick={()=>this.deleteip(item)} type="close-circle" /></List.Item>}
            /> 
            </Col>
            </Row>
        )
    }
}
export default BlaskList