import React,{Fragment} from 'react'
import Temp from './temp';
import { Col, Card,Row,Icon, Button,Modal,message,Form ,Input} from 'antd';
import Axios from "axios"
class Myadmin extends React.Component{

    constructor(props){
        super(props)
        this.adminit=this.adminit.bind(this)
        this.state={
            usernam:"",
            paswd:"",
            money:0,
            ispswd:false,
            visible:false,
        }
        this.adminit()
    }
    adminit(){
        Axios.post("/admin/get").then((res)=>{

            this.setState({
                usernam:res.data.data.Username,
                paswd:res.data.data.Paswd,
                money:res.data.data.Money

            })
        })
    }
    handleSubmit = e => {
        var data={}
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
         var pswd=values["pswd"]
          data={"pawsd":pswd}
        });
        var url="/admin/updatepaswd"
        Axios.post(url,data).then(function(response){
             
            if(response.data.code==0){
                message.success("")
            }
          })
      };
         
    showModal = () => {
        this.setState({
          visible: true,
        });
      };
    
      handleOk = e => {
        console.log(e);
        this.setState({
          visible: false,
        });
      };
    
      handleCancel = e => {
        console.log(e);
        this.setState({
          visible: false,
        });
      };
    render(){
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: { span: 6 },
            wrapperCol: { span: 14 },
          };
        return(
            <Row>
                <Temp/>
                <Col style={{backgroundColor:"#ffffff"}} span={20}>
                    <Card style={{paddingTop:50,paddingLeft:100}}>
                        <Row style={{paddingTop:5,fontSize:25}}><Icon type="user" /><label>:{this.state.usernam}</label></Row>
                        <Row style={{paddingTop:20,fontSize:25}}><Icon type="pause-circle" /><label>:{this.state.ispswd==true?this.state.paswd:"**********"}</label><Icon onClick={()=>{
                            this.setState({
                                ispswd:!this.state.ispswd
                            })
                        }} type="eye" /></Row>
                        <Row><Button onClick={()=>this.showModal()}></Button></Row>
                        <Row style={{paddingTop:20,fontSize:25}}><Icon type="transaction" /><label>:{this.state.money}</label></Row>
                     
                    </Card>
                </Col>
                <Modal
                    title=""
                    visible={this.state.visible}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                >
                <Form {...formItemLayout} onSubmit={this.handleSubmit}>
                        <Form.Item label="">
                        {getFieldDecorator('pswd', {
                            rules: [
                            {
                                
                                message: '',
                            },
                            {
                                required: true,
                                message: 'Please input your phone'
                            },
                            ],
                        })(<Input />)}
                        </Form.Item>
                    
                      
                        <Form.Item wrapperCol={{ span: 12, offset: 6 }}>
                        <Button type="primary" htmlType="submit"></Button>
                        </Form.Item>
                       
                        </Form>
        </Modal>
                </Row>
        )
    }
}
const createForm = Form.create;

Myadmin = createForm()(Myadmin);
export default Myadmin