import React from 'react'
import {HashRouter,Route,Switch} from 'react-router-dom'
import ObjiectList from '../pages/objectlist'
import CustomerList from '../pages/Customerlist'
import Addcustomer from '../pages/addcustomer'
import AddcustomerList from '../pages/addcustomerlist'
import Addobject from '../pages/bojectadd'
import Avatar from '../pages/addboject'
import GiftList from '../pages/giftlist'
import AddAdmin from '../pages/addAdmin'
import Admin from '../pages/admin'
import Index from '../pages/index'
import BlaskList from '../pages/blacklist'
import Login from '../pages/login';
import Auto from '../pages/auto'
import Myadmin from '../pages/myadmin'
import UserGifts from '../pages/usergifts'
const Root=()=>(

    <HashRouter> 
        <Switch>

            <Route exact  path="/" component={Login}></Route>
            <Route  exact path="/index" component={ObjiectList}></Route>
            <Route  exact path="/admin" component={Admin}></Route>
            <Route  exact path="/gifts" component={GiftList}></Route>
            <Route  exact path="/usergifts/:userid/:name" component={UserGifts}></Route>
            <Route  exact path="/auto" component={Auto}></Route>
            <Route  exact path="/myadmin" component={Myadmin}></Route>
            <Route  exact path="/addobject/:id" component={Addobject}></Route>
            <Route  exact path="/customerlist/:id/:page" component={CustomerList}></Route>
            <Route  exact path="/addcustomer/:objid/:id/:page" component={Addcustomer}></Route>
            <Route  exact path="/addcustomerlist/:objid" component={AddcustomerList}></Route>
            <Route  exact path="/blacklist" component={BlaskList}></Route>
            
        </Switch>
    </HashRouter>
)
export default Root;