import React,{Fragment} from 'react'
import { Row,Breadcrumb,Table, Icon,Input,Col, Card, Form,Upload, InputNumber ,Button,message,Switch, Spin} from 'antd';
import TextArea from 'antd/lib/input/TextArea';
import Axios from 'axios'
import Temp from './temp'
const createForm = Form.create;
function sleep(ms) {
  return new Promise(resolve => setTimeout(resolve, ms));
  } 
function beforeUpload(file) {
    const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
    if (!isJpgOrPng) {
      message.error('You can only upload JPG/PNG file!');
    }
    const isLt2M = file.size / 1024 / 1024 < 1000;
    if (!isLt2M) {
      message.error('2M!');
    }
    return isJpgOrPng && isLt2M;
  }
class AddcustomerList extends React.Component{

    constructor(props){
        super(props)
       this.addcustome=this.addcustome.bind(this)
       this.addcustomelist=this.addcustomelist.bind(this)
     
      this.state={
        fileList:[],
        objid:parseInt(this.props.match.params.objid),
        loading: false, 
       
      }
      
      
     
        
    }
   
    handleChange = info => {
        let fileList = [...info.fileList];
    
        // 1. Limit the number of uploaded files
        // Only to show two recent uploaded files, and old ones will be replaced by the new
    
        // 2. Read from response and show file link
        fileList = fileList.map(file => {
            console.log(file)
          if (file.response) {
            // Component will show file.url as link
            file.url = file.response.data;
         
          }

          return file;
        });
      
        this.setState({ fileList });
   
    }

    addcustome(objid,name,image){
        var data={
            "name":name,
            "phone":" ",
            "objid":objid,
            "describe":"",
            "image1":image,
            "image2":"",
            "image3":"",
            "image4":"",
            "image5":"",
            "staus":"yes",
            
        }
        
        var url="/user/add"
        Axios.post(url,data).then((res)=>{
        if(res.data.code==0){
            message.success("")
        }})
    }
    
    
    async addcustomelist(){
        var {fileList}=this.state
        this.setState({
          loading:true
        })
        for(var i=0;i<fileList.length;i++){
         
          var file=fileList[i]
          if (file.response) {
              if(file.response.code==0){
                await sleep(80)
            // Component will show file.url as link
             this.addcustome(this.state.objid,file.name.split(".")[0] ,file.url)
              }
          }

          
        }
        this.setState({
          loading:false
        })
        this.props.history.push("/customerlist/"+this.state.objid+"/"+1)

    }
    
    
    render()
    {

        const props = {
            action: '/image/upload',
            onChange: this.handleChange,
            multiple: true,
            name:"avatar"
          };
  
        return(
           <Spin tip="..." spinning={this.state.loading} delay={100}>
          <Row>
          <Temp/>
          <Col style={{backgroundColor:"#ffffff"}} span={20}>
            <div style={{width:"100%"}}>
             
                <Row>
                <Row>  
            <Breadcrumb>
            <Breadcrumb.Item></Breadcrumb.Item>
            <Breadcrumb.Item></Breadcrumb.Item>
            <Breadcrumb.Item></Breadcrumb.Item>
             </Breadcrumb>
         </Row>
                    <Card>
                     
                <Upload {...props} fileList={this.state.fileList}>
                        <Button>
                            <Icon type="upload" /> 
                        </Button>
                </Upload>
                    </Card>
                    
                </Row>
            <Row style={{ paddingLeft:"40%"}}><Button onClick={()=>this.addcustomelist()}></Button></Row>
            </div>
            </Col>
            </Row>
            </Spin>
        )
    }

}

export default AddcustomerList