    normFile = e => {
        console.log('Upload event:', e);
        if (Array.isArray(e)) {
            return e;
        }
        return e && e.fileList;
    };
    add = () => {
        const { form } = this.props;
        // can use data-binding to get
        const keys = form.getFieldValue('keys');
        const nextKeys = keys.concat(id++);
        // can use data-binding to set
        // important! notify form to detect changes
        form.setFieldsValue({
            keys: nextKeys,
        });
    };
    remove = k => {
      const { form } = this.props;
      // can use data-binding to get
      const keys = form.getFieldValue('keys');
      // We need at least one passenger
    //   if (keys.length === 1) {
    //     return;
    //   }

      // can use data-binding to set
      form.setFieldsValue({
        keys: keys.filter(key => key !== k),
      });
    };
    
     handleEditorChange1 = (editorState1) => {
      this.setState({ editorState1 })
  }

  handleEditorChange2 = (editorState2) => {
    this.setState({ editorState2 })
}
  
uploadHandler1 = (info) => {
      if (info.file.status === 'uploading') {
        
        return;
      }
     
      if (info.file.response.code == 0) {
        // Get this url from response in real world.

        this.setState({
          editorState1: ContentUtils.insertMedias(this.state.editorState1, [{
            type: 'IMAGE',
            url: info.file.response.data
          }])
        })
      }

   

  }
  uploadHandler2 = (info) => {
    if (info.file.status === 'uploading') {
      
      return;
    }
   
    if (info.file.response.code == 0) {
      // Get this url from response in real world.

      this.setState({
        editorState2: ContentUtils.insertMedias(this.state.editorState2, [{
          type: 'IMAGE',
          url: info.file.response.data
        }])
      })
    }

 

}
