import React,{Fragment} from 'react'
import { Row,Breadcrumb,Table, Icon,Input,Col, Button,InputNumber,Modal,Popconfirm,message} from 'antd';
import Addauto from './addauto'
import Axios from 'axios'
import Temp from './temp'
class Auto extends React.Component{

  constructor(props){
    super(props)
    this.getlist=this.getlist.bind(this)
    this.state = {
      data: [],
      pagination: {},
      loading: false,
    };
    
    this.columns=[
      {
        title: '',
        dataIndex: 'Title',
        key: 'Title',
        render(text) {
          return <a href="">{text}</a>;
        }
      },
      {
        title: '',
        dataIndex: 'Interval',
        key: 'Interval',
        render(text) {
          return <a href="#">{text}</a>;
        }
      },
      {
        title: '',
        dataIndex: 'Click',
        key: 'Click',
        render(text) {
          return <a href="#">{text}</a>;
        }
      },
      {
        title: '',
        dataIndex: 'opertor',
        key: 'opertor',
        render:(text,red)=> {
          return  <Popconfirm title="?" onConfirm={() =>this.deleteauto(red.Objid)}>
          <a href="javascript:;"></a>
          </Popconfirm>;
        }
      },
    ]

  }
  deleteauto= id =>{
    var url="/auto/delete"
    var self=this
    Axios.post(url,{"id":id}).then((res)=>{
  
      if(res.data.code==0){
        const data = [...self.state.data];
        this.setState({ data: data.filter(item => item.Objid !== id) });
        message.success('');

      }else{
        message.error('');

      }
  
    }
    )
  
  };
  componentDidMount(props){
    
    this.getlist(1)
  }
  getlist(page){
    var url="/auto/list"
    var self=this
    this.setState({ loading: true });
    Axios.post(url,{"page":page}).then(
      function(response){

        if(response.data.code==0&response.data.data!=null){
          var pagination = { ...self.state.pagination };
          pagination.total = response.data.data.length;
          self.setState({
            loading: false,
            data:response.data.data,
            pagination:pagination
          })
        }
      })

    
  }
   
    showModal = () => {
        this.setState({
          visible: true,
        });
      };
    
      handleOk = e => {
        console.log(e);
        this.setState({
          visible: false,
        });
      };
    
      handleCancel = e => {
        console.log(e);
        this.setState({
          visible: false,
        });
      };
      
    render(){
        return(
          <Row>
          <Temp/>
          <Col style={{backgroundColor:"#ffffff"}} span={20}>
            <div style={{width:"100%"}}>
                  <Row>  
                <Breadcrumb>
                <Breadcrumb.Item></Breadcrumb.Item>
                <Breadcrumb.Item></Breadcrumb.Item>
                <Breadcrumb.Item></Breadcrumb.Item>
                 </Breadcrumb>
             </Row>
             <Row style={{paddingTop:10}} >
                 <Col span="10">
                     <h1>
                     
                     </h1>
                     </Col>
                 <Col span="10"></Col>
             </Row>
             <Row>
             <Col span="10"><h3></h3></Col>
             <Col span="13" style={{textAlign:"right"}}>

             <Button type="primary" onClick={this.showModal}>+</Button>
             </Col>
             </Row>
             <Row>
             <Table columns={this.columns} dataSource={this.state.data}/>
             </Row>
             <Modal  title=""
                visible={this.state.visible}
                onOk={this.handleOk}
                onCancel={this.handleCancel}
               >
                    <Addauto />
               </Modal>
            </div>
            </Col>
            </Row>
        )
    }
}

    
  
   
    
export default Auto